body {
    margin: 0;
    padding: 0;
    /* background-color: #1d2634;
    color: #9e9ea4; */
    font-family: 'Montserrat', sans-serif;
  }
  
  .icon {
    vertical-align: middle;
    line-height: 1px;
    font-size: 20px;
  }
  .icon_header {
    vertical-align: middle;
    line-height: 1px;
    font-size: 26px;
  }
  .icon, .icon_header {
    margin-right: 5px;
  }
  
  .close_icon {
    color: red;
    margin-left: 30px;
    margin-top: 10px;
    cursor: pointer;
  }
  
  .dfms-logo{
    background:url(../../../../docs/DRMS.png)
  }
  .grid-container {
    display: grid;
    grid-template-columns: 260px 1fr 1fr 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas:
      'sidebar header header header'
      'sidebar main main main';
    height: 100vh;
  }
  
  /* Header  */
  .header {
    grid-area: header;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 0 30px;
    background: transparent !important;
    /* border-bottom: solid .1px #EBEBEB; */

    /* box-shadow: 0 6px 7px -3px rgba(0, 0, 0, 0.35); */
  }
  
  .menu-icon {
    display: none;
  }
  /* End Header  */
  
  /* Sidebar  */
  .app-btn-def{
    outline-style: groove;
    outline-width: 1px;
  }
  .manage-acc-btn{
    outline-style: groove;
    outline-width: 1px;
    padding: 4px;
    margin: auto;
    margin-top: 40px;
    margin-bottom: -20px;
  }
  .new-account-tickets-circle:active {
    outline-style: groove;
  }
    .new-account-tickets{
    outline-style: groove;
    outline-width: 1px;
    padding-bottom: 0;
    padding-top: 5px !important;
    margin-bottom: 0px;

  }
  #sidebar {
    grid-area: sidebar;
    height: 100%;
    background-color: #384969f7;
    overflow-y: auto;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    z-index: 2 !important;
  }
  .one-z-index{
    z-index:6;
    /* background: white; */
  }
  .two-z-index{
    z-index:6;
    /* background: white; */
  }
  .three-z-index{
    z-index: 3 !important;
    /* background: white; */
  }
  .four-z-index{
    z-index:13;
    
  }
  .sidebar-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 25% ;
    margin-bottom: 30px;
  }
  
  .sidebar-title > span {
    display: none;
  }
  
  .sidebar-brand {
    margin-top: 15px;
    font-size: 20px;
    font-weight: 700;
  }
  
  .sidebar-list {
    padding: 0;
    list-style-type: none;
  }
  
  .sidebar-list-item {
    padding: 20px 20px 20px 20px;
    font-size: 18px;
  }
  
  .sidebar-list-item:hover {
    /* background-color: rgba(255, 255, 255, 0.2); */
    cursor: pointer;
    color: blue;
  }
  .sidebar-list-item > a {
    text-decoration: none;
    color: #9e9ea4;
  }
    .sidebar-list-item > a::after{
    color: blue !important;
  }
  
  .sidebar-responsive {
    display: inline !important;
    position: absolute;
    /*
      we want the z-index of the sidebar higher so that
      the charts are not showing over the sidebar 
      on small screens
    */
    z-index: 2 !important;
  }
  
  /* End Sidebar  */
  
  
  /* Main  */  
  .main-container {
    grid-area: main;
    overflow-y: auto;
    padding: 20px 20px;
    color: rgba(255, 255, 255, 0.95);
    
  }
  
  .main-title {
    display: flex;
    justify-content: space-between;
  }
  
  .main-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    margin: 15px 0;
  }
  
  .card {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    background: inherit;
    /* background: transparent !important; */
    color: rgb(152, 147, 147);
  }
  
  .card_icon{
    color: green;
  }
  .card-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .card-inner > .card_icon {
    font-size: 25px;
  }
  
  .charts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-top: 60px;
    margin-left: -2.5em !important;
    height: 300px;
    
  }
  
  /* End Main  */
  
  
  /* Medium <= 992px */
    
  @media screen and (max-width: 992px) {
    .grid-container {
      grid-template-columns: 1fr;
      grid-template-rows: 0.2fr 3fr;
      grid-template-areas:
        'header'
        'main';
    }
  
    #sidebar {
      display: none;
    }
  
    .menu-icon {
      display: inline;
    }
  
    .sidebar-title > span {
      display: inline;
    }
  }
  /* Small <= 768px */
    
  @media screen and (max-width: 768px) {
    .main-cards {
      grid-template-columns: 1fr;
      gap: 10px;
      margin-bottom: 0;
    }
  
    .charts {
      grid-template-columns: 1fr;
      margin-top: 30px;
    }
  }
  
  /* Extra Small <= 576px */
  
  @media screen and (max-width: 576px) {
    .hedaer-left {
      display: none;
    }
  }